
<template>
<div>
  <add></add>
  <el-table
    :data="tableData"
    style="width: 100%"
    max-height="250">
    <el-table-column
      prop="id"
      label="图书编号"
      align="center"
      >
    </el-table-column>
    <el-table-column
      prop="title"
      label="图书名称"
      align="center"
   >
    </el-table-column>
    <el-table-column
      prop="author"
      label="作者"
      align="center"
     >
    </el-table-column>
    <el-table-column
      fixed="right"
      label="操作"
      align="center"
      >
      <template slot-scope="scope">
        <el-button
          @click.native.prevent="deleteRow(scope.$index, tableData)"
          type="text"
          size="small">
          移除
        </el-button>
      </template>
    </el-table-column>
  </el-table>
    <el-button @click="logout" style="float:right;margin:5%" >退出登录</el-button>
  </div>
</template>

<script>
import add from "../components/addButton";
import { Message } from "element-ui";
export default {
   mounted () {
    this.showBook();
  },
  methods: {
    showBook(){
      this.$http({
        url: "http://127.0.0.1:8100/book/getList",
        method: "GET",
        headers:{
          "Content-Type": "application/json"
        },
        withCredentials: true
      }).then(
        function(response){
          console.log(response.data)
          if(response.data.code === 0){
            this.tableData = response.data.data
          } else {
            Message.warning(response.data.msg);
          }
        }
      )

    },
    logout() {
      this.$confirm("确定退出登录吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$http({
          url: "http://127.0.0.1:8100/user/logout",
          method: "GET",
          headers: {
            "Content-Type": "application/json"
          },
          withCredentials: true
        }).then(function(response) {
          console.log(response.data.code);
          if (response.data.code === 0) {
            Message.success("退出成功！");
            this.$router.push({ name: "login" });
          } else {
            Message.warning(response.data.msg);
          }
        });
      });
    },
    deleteRow(index, rows) {
      console.log("index"+index)
      this.$http({
        url: "http://127.0.0.1:8100/book/delete",
        method: "GET",
        params: {
          id: this.tableData[index].id
        },
        headers: {
          "Content-Type": "application/json"
        },
        withCredentials: true
      }).then(function(response) {
        console.log(response.data.code)
        if (response.data.code === 0) {
          Message.success("删除成功");
        } else {
          Message.warning(response.data.msg);
        }
      });
      rows.splice(index, 1);
    }
  },

  data() {
    return {
      tableData: [
        {
          id: 0,
          title: "",
          author: ""
        }
      ]
    };
  }
};
</script>




